/* Global variables - Can be used in every components by using var(--var-name) method*/
:root {
  /* Body */
  --body-bg: #f5f8fa;

  /* Borders */
  --laravel-border-color: darken(var(--body-bg), 10%);
  --list-group-border: --laravel-border-color;
  --navbar-default-border: --laravel-border-color;
  --panel-default-border: --laravel-border-color;
  --panel-inner-border: --laravel-border-color;

  /* Brands */
  --brand-primary: #20a0ff;
  --brand-primary-light: #58B7FF;
  --brand-primary-dark: #1D8CE0;

  --brand-secondary: #ddd;
  --brand-secondary-light: #eee;
  --brand-secondary-dark: #ccc;

  --brand-info: #50BFFF;
  --brand-success: #13ce66;
  --brand-warning: #f7ba2a;
  --brand-danger: #ff4949;

  /* Borders */
  --border-secondary: #ccc;

  /* Typography */
  --icon-font-path: "";
  --font-family-sans-serif: "ProximaNova","Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-size-base: 14px;
  --line-height-base: 1.5;
  --text-color: #636b6f;

  /* Navbar */
  --navbar-default-bg: #20a0ff;
  --navbar-height: 50px;
  --nav-color: #FFF;
  --nav-bg: #20a0ff;

  /* Buttons */
  --btn-default-color: var(--text-color);

  /* Inputs */
  --input-border: lighten(var(--text-color), 40%);
  --input-border-focus: lighten(var(--brand-primary), 25%);
  --input-color-placeholder: lighten(var(--text-color), 30%);

  /* Panels */
  --panel-default-heading-bg: #fff;

  /* Screen size */
  /* Variable not work with @media */
  --max-mobile-screen-width: 600px; /* mobile: 0px - 600px */
  --max-tablet-screen-width: 768px; /* table: 600px - 768px */
  /* desktop: > 768px */

  --default-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.65);
}

/* Global style */
* {
  box-sizing: border-box;
}

html, body, #app {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
}

html {
  font-size: 1em;
  line-height: var(--line-height-base);
  overflow-x: hidden;
}

body {
  color: var(--text-color);
  background-color: var(--body-bg);
  font-size: var(--font-size-base);
  font-family: var(--font-family-sans-serif);
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-button {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-thumb {
  background: rgba(150, 150, 150, 0.4);
  border: none;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(150, 150, 150, 0.5);
}

::-webkit-scrollbar-thumb:active {
  background: rgba(150, 150, 150, 0.6);
}

::-webkit-scrollbar-track {
  /* background: var(--body-bg); */
  background: transparent;
  border: none;
  border-radius: 2px;
}

::-webkit-scrollbar-track:hover {
  /* background: var(--body-bg); */
  background: transparent;
}

::-webkit-scrollbar-track:active {
  /* background: var(--body-bg); */
  background: transparent;
}

::-webkit-scrollbar-corner {
  /* background: var(--body-bg); */
  background: transparent;
}

/* Brand class */
.element-link {
  cursor: pointer;
  text-align: center;
  min-width: 64px;
  min-height: 24px;
  vertical-align: middle;
}

.element-button {
  cursor: pointer;
  border: none;
}

.element-button:hover, .element-button:focus {
  outline: none;
  border: none;
}

.element-button:active {
  outline: none;
  border: none;
}

.element-primary {
  color: var(--brand-primary);
}

.element-primary:hover, .element-primary:focus {
  color: var(--brand-primary-light);
}

.element-primary:active {
  color: var(--brand-primary-dark);
}

.element-bg-primary {
  background: var(--brand-primary);
  color: white;
}

.element-bg-primary:hover, .element-bg-primary:focus {
  background: var(--brand-primary-light);
}

.element-bg-primary:active {
  background: var(--brand-primary-dark);
}

.element-bg-secondary {
  background: var(--brand-secondary);
}

.element-bg-secondary:hover, .element-bg-secondary:focus {
  background: var(--brand-secondary-light);
}

.element-bg-secondary:active {
  background: var(--brand-secondary-dark);
}



