/* Proxima Nova */

@font-face {
    font-family: 'ProximaNova';
    src: url('../fonts/ProximaNova/ProximaNovaAlt-Regular.otf') format("opentype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'ProximaNova';
    src: url('../fonts/ProximaNova/ProximaNovaAlt-Semibold.otf') format("opentype");
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'ProximaNova';
    src: url('../fonts/ProximaNova/ProximaNovaAlt-Regular-Italic.otf') format("opentype");
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'ProximaNova';
    src: url('../fonts/ProximaNova/ProximaNovaAlt-Semibold-Italic.otf') format("opentype");
    font-weight: bold;
    font-style: italic;
}



